window.axios  = require('axios');

export default{

    methods : {

      // Conference module
      getConferenceList: function() {

          let _this = this;
          this.conference_list = [];
          let searchQuery = (this.searchQuery!=="")?this.searchQuery:"all";

          let encodedUserInfo = this.getCookie("userInfo");
          let decodedUserInfoString = decodeURIComponent(encodedUserInfo);
          let userInfo = JSON.parse(decodedUserInfoString);
          let userID = userInfo["uuid"];


          let get_url = "./conference/list/1/" + searchQuery; // + userID;
          //console.log("Retrieving data from : " + get_url);

          // VueJS ajax call-1
          axios.get(get_url)
              .then((dataResponse) => {

                  //console.log("Conference Registration Result : ");
                  //console.log(dataResponse);
                  //console.log(dataResponse.data["bean"]);
                  //console.log((dataResponse.data["bean"]).slice(0, 5));

                  if (dataResponse.data["actionResult"] === 1) {
                      _this.conference_list = (dataResponse.data["bean"]);//.slice(0, 10);

                      _this.conference_list.forEach(conf => {

                          conf["name_str"] = conf.name;
                          if(conf.name.length>25)
                          {
                            conf.name = conf.name.substr(0,25) + "..";
                          }

                          if(conf.scheduledDateTime !== null)
                          {
                              conf.scheduledDateTime = new Date(conf.scheduledDateTime).toLocaleString()
                                                                                       .slice(0, 17)
                                                                                       .replace(',', '')
                                                                                       .replace('T',' ');
                          }

                          if (conf.participantList !== null)
                              conf.participants_info = conf.participantList;
                          else
                              conf.participants_info = [];


                          if(conf.isLive!==null && conf.isLive===1)
                          {
                              conf.isLive = "LIVE";
                          }
                      });

                      if(_this.conference_list.length>0)
                          _this.ongoingConf = _this.conference_list[0];

                      _this.conference_list.forEach(conf => {

                          let participant_names = [];
                          let participants_info = conf.participants_info;
                          let isCurrentlyLoggedInUserAParticipant = false;

                          participants_info.forEach(participant => {
                              if(participant["id"] === _this.userId)
                                isCurrentlyLoggedInUserAParticipant = true;

                              participant_names.push(participant.firstName + " " + participant.lastName);
                          });

                          conf["isParticipant"] = isCurrentlyLoggedInUserAParticipant;

                          let participants_info_str = participant_names.join(", ");
                          conf["participants_info_str"] = participants_info_str;
                      });

                  } else {
                      _this.conf_service_error = dataResponse;
                      console.error("Error", dataResponse);
                      alert("Error : " + dataResponse)
                  }


              })
              .catch(function(errorResponse) {

                  console.log("ERROR MS - ", errorResponse);
                  let exceptionMsg = errorResponse.body["error"];

                  if(errorResponse["status"]===403)
                    _this.conf_service_error = "Permission Denied to retrieve conferences";
                  else
                    _this.conf_service_error = exceptionMsg;

                  return false;
              });

      },
      getMyConferenceList: function() {

          let _this = this;
          this.my_conference_list = [];

          let get_url = "./conference/my-list/1/";
          //console.log("Retrieving data from : " + get_url);

          // VueJS ajax call-1
          axios.get(get_url)
              .then((dataResponse) => {

                  //console.log("Conference Registration Result : ");
                  //console.log(dataResponse);
                  //console.log(dataResponse.data["bean"]);
                  //console.log((dataResponse.data["bean"]).slice(0, 5));

                  if (dataResponse.data["actionResult"] === 1) {
                      _this.my_conference_list = (dataResponse.data["bean"]);//.slice(0, 10);

                      _this.my_conference_list.forEach(conf => {

                          conf["name_str"] = conf.name;
                          if(conf.name.length>15)
                          {
                            conf.name = conf.name.substr(0,15) + "..";
                          }

                          if(conf.scheduledDateTime !== null)
                          {
                              conf.scheduledDateTime = new Date(conf.scheduledDateTime).toLocaleString()
                                                                                       .slice(0, 17)
                                                                                       .replace(',', '')
                                                                                       .replace('T',' ')
                                                                                       .split(" ")[1];
                          }

                          if (conf.participantList !== null)
                              conf.participants_info = conf.participantList;
                          else
                              conf.participants_info = [];


                          if(conf.isLive!==null && conf.isLive===1)
                          {
                              conf.isLive = "LIVE";
                          }
                      });

                      _this.my_conference_list.forEach(conf => {

                          let participant_names = [];
                          let participants_info = conf.participants_info;
                          participants_info.forEach(participant => {
                              participant_names.push(participant.firstName + " " + participant.lastName);
                          });


                          let participants_info_str = participant_names.join(", ");
                          conf["participants_info_str"] = participants_info_str;
                      });

                  } else {
                      _this.conf_service_error = dataResponse;
                      console.error("Error", dataResponse);
                      alert("Error : " + dataResponse)
                  }


              })
              .catch(function(errorResponse) {

                  console.log("ERROR MS - ", errorResponse);
                  let exceptionMsg = errorResponse.body["error"];

                  if(errorResponse["status"]===403)
                    _this.conf_service_error = "Permission Denied to retrieve conferences";
                  else
                    _this.conf_service_error = exceptionMsg;

                  return false;
              });

      },
      getStarredConferences: function() {

          let _this = this;
          this.conference_list = [];

          let encodedUserInfo = this.getCookie("userInfo");
          let decodedUserInfoString = decodeURIComponent(encodedUserInfo);
          let userInfo = JSON.parse(decodedUserInfoString);
          let userID = userInfo["uuid"];

          let get_url = "./../rest/conference-service/gsc/" + userID;
          //console.log("Retrieving data from : " + get_url);


          // VueJS ajax call-1
          axios.get(get_url)
              .then((dataResponse) =>  {

                  console.log("Starred Conferences Result : ");
                  console.log(dataResponse);
                  //this.conference_list = dataResponse.data;

                  if (dataResponse.data["success"] === true) {

                      _this.starred_conferences_list = (dataResponse.data["result"]).slice(0, 5);
                      _this.starred_conferences_list.forEach(conf => {

                          let participant_names = [];

                          let participants_info = conf.participants_info;
                          participants_info.forEach(participant => {
                              participant_names.push(participant.pFirstName);
                          });


                          let participants_info_str = participant_names.join(", ");
                          conf["participants_info_str"] = participants_info_str;
                      });


                  } else {
                      alert("Error : " + dataResponse)
                  }


              });

      },

      // Workstatuses module
      getWorkStatuses(){

          let _this = this;
          //this.conference_list = [];
          let wsSearchQuery = (this.wsSearchQuery!=="")?this.wsSearchQuery:"all";
          let get_url = "./work-feed/list/1/" + wsSearchQuery; // + userID;
          //console.log("Retrieving data from : " + get_url);

          // VueJS ajax call-1
          axios.get(get_url)
              .then((dataResponse) => {

                  //console.log("Conference Registration Result : ");
                  //console.log(dataResponse);
                  let list =  dataResponse.data;
                  list.forEach(ws => {
                      ws["dateString"] = moment(ws["date"],"YYYY-MM-DD hh:mm:ss").fromNow();
                      ws["dateFmt"] = moment(ws["date"],"YYYY-MM-DD hh:mm:ss").format('MMMM Do YYYY, h:mm:ss a');
                      _this.workStatusList.push(ws);
                  });

                  //moment("2020-06-10 19:18:11","YYYY-MM-DD hh:mm:ss").fromNow();
              })
              .catch(function(errorResponse) {

                  console.log("ERROR MS - ", errorResponse);
                  let exceptionMsg = errorResponse.body["error"];

                  if(errorResponse["status"]===403)
                    _this.ws_list_error = "Permission Denied to retrieve conferences";
                  else
                    _this.ws_list_error = exceptionMsg;

                  return false;
              });
      },
      updateWorkStatus(){

        if(this.workStatus.trim()==="")
            return false; // stop here if form is invalid.

        let btnText = document.getElementById("wsButton").innerHTML;
        document.getElementById("wsButton").innerHTML = "Updating..";
        this.disableHTMLElement(document.getElementById("wsButton"));

        let _this_component = this;
        let post_url = "./work-feed/update-work-status";
        console.log("Posting data to : " + post_url);

        let form = {
            description     :   this.workStatus
        }

        let formString = JSON.stringify(form,null,null);

        // VueJS ajax call-1
        axios.post(post_url, form)
                  .then((dataResponse) => {

                    console.log("Save result : ");
                    console.log(dataResponse);

                    if(dataResponse.data["actionResult"]===1)
                    {
                        _this_component.newUserInfo = dataResponse.data["bean"];

                        UIkit.notification("<span uk-icon='icon: check;ratio: 0.65'></span> Status updated successfully",{
                            status: 'success',
                            pos: 'top-right',
                            timeout: 5000
                        });

                        document.getElementById("wsButton").innerHTML = btnText;
                        this.enableHTMLElement(document.getElementById("wsButton"));

                        this.workStatus="";
                        this.updateStatusContainer();
                        this.workStatusList = [];
                        this.getWorkStatuses();
                    }
                    else
                    {
                        //alert("Error : " + dataResponse)
                        let exceptionMsg = dataResponse.data["exception"];
                        UIkit.notification("<span uk-icon='icon: warning ;ratio: 0.65'></span> " + exceptionMsg + ".", {
                                            status: 'danger',
                                            pos: 'top-right',
                                            timeout: 500000
                                        });


                        document.getElementById("wsButton").innerHTML = btnText;
                        this.enableHTMLElement(document.getElementById("wsButton"));
                        return false;
                    }


            })
            .catch(function(errorResponse) {

                console.log("ERROR MS - ", errorResponse);
                let exceptionMsg = errorResponse.body["exception"];

                UIkit.notification("<span uk-icon='icon: warning ;ratio: 0.65'></span> " + exceptionMsg + ".", {
                                    status: 'danger',
                                    pos: 'top-right',
                                    timeout: 500000
                                });


                document.getElementById("wsButton").innerHTML = btnText;
                this.enableHTMLElement(document.getElementById("wsButton"));
                return false;
            });
      },
      updateStatusContainer(){

      },
    }

}
